<template>
  <div>
       <v-data-table
    :loading="loadingData"
      :headers="headers"
      :items="hosts"
      :search="search"
    >
    <template v-slot:item.edit="{ item }">
        <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <span @click="setEditUser(item)">
          <v-icon
          color="primary"
          dark
          medium
          v-bind="attrs"
          v-on="on"
        >mdi-pencil-box-multiple</v-icon>
        </span>
      </template>
      <span>Düzenle</span>
        </v-tooltip>
            <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <span @click="textConfirm = `${item.fullname} isimli ${item.busLicense} Plakalı servisi sistemden kaldırıyorsunuz. Emin Misiniz?`;processData={...item};dialog3=true">
          <v-icon
          color="primary"
          dark
          medium
          v-bind="attrs"
          v-on="on"
        >mdi-close-box</v-icon>
        </span>
      </template>
      <span>Yetkiliyi Kaldır</span>
        </v-tooltip>
    </template>
    </v-data-table>
    <confirm :dialog="dialog3" @dialog="dialog3=$event" @cancel="dialog3=false" @confirm="deleteItem" />
    <edit-host @setPutUser="setEditUserData = $event" :dialog="editDialog" :index="editIndex" @dialog="editDialog=$event" :user="editUser"></edit-host>
  </div>
</template>

<script>
import editHost from './editHost'
export default {
    components:{
        editHost
    },

data(){
    return{
      setEditUserData:null,
        editUser:[],
        editDialog:false,
        editIndex:-1,
        hosts:[],
        loadingData:false,
        search:"",
        headers: [
          {
            text: "Yetkili Adı Soyadı",
            align: 'start',
            filterable: true,
            value: 'fullname',
          },
          { text: 'Telefon', value: 'phone' },
          { text: 'Servis Firması', value: 'busName' },
          { text: 'Servis Plakası', value: 'busLicense' },
          { text: 'Düzenle', value: 'edit' },
        ],
        processData:{},
        dialog3:false
    }
},
watch:{
  setEditUserData(data){
    if(data!=null){
      console.log(data);

      this.hosts[data.index].fullname = data.user.fullname
      this.hosts[data.index].busName = data.user.busName
      this.hosts[data.index].busLicense = data.user.busLicense
      this.hosts[data.index].phone = data.user.phone
      if(data.image!=''){
        this.hosts[data.index].image = data.image
     }
      this.setEditUserData=null
    }
  },
},
methods:{
   setEditUser(row){
      let pos = this.hosts.map(function(e) { return e.id; }).indexOf(row.id);
           this.editUser = {
             fullname : row.fullname,
             busName : row.busName,
             busLicense : row.busLicense,
             id:row.id,
             image:row.image,
             username : row.username,
             phone:row.phone,
          //   index:pos
           }
              this.editIndex=pos
              this.editDialog=true
   },
    deleteItem(){
        this.$api.put('host',this.processData.id,{isActive:0}).then(res=>{
           let pos = this.hosts.map(function(e) { return e.id; }).indexOf(this.processData.id);
           if(res.result==1){
               this.hosts.splice(pos,1)
               this.dialog3=false
           }

        })
    },
    initHosts(){
        this.$api.get('host').then(res=>{
            if(res.count!=0){
              res.body.forEach(bdy=>{
                bdy.image = bdy.image.replace('public/',this.$api.BASE_API_URL)
              })
                this.hosts = res.body
                this.loadingData=false
            }
            else{
                this.loadingData = false
            }
        })
    }
},
mounted(){
    this.initHosts()
}
}
</script>

