<template>
  <v-dialog v-model="dialog" persistent max-width="600px">
       <v-card>
        <v-card-title>
          <span class="headline">Servis Yetkilisi Düzenleme Ekranı</span>
        </v-card-title>
        <v-card-text>
          <v-container>
             <v-alert
               v-if="err!=''"
      dense
      prominent
      text
      :type="'error'"
    >
    {{err}}
    </v-alert>
            <v-row>
              <v-col cols="12" class="text-center">
  <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <span @click="newImage=null;imageData=''">
          <v-icon
          color="danger"
          dark
          medium
          v-bind="attrs"
          v-on="on"
        >mdi-close-box-multiple</v-icon>
        </span>
      </template>
      <span>Resimi Kaldır</span>
        </v-tooltip>
  <v-list-item-avatar size="170" color="grey darken-3">

          <v-img
            class="elevation-6"
            :src="imageData!=''?imageData:user.image==0?'https://s3.amazonaws.com/37assets/svn/765-default-avatar.png':user.image"
            alt="Resim Bulunmamaktadır"
             ></v-img>
        </v-list-item-avatar>
    <input type="file" name="" @change="setImage" class="d-none" ref="fileInput" id="">
       <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <span @click="$refs['fileInput'].click()" class="ml-3">
          <v-icon
          color="primary"
          dark
          medium
          v-bind="attrs"
          v-on="on"
        >mdi-pencil-box-multiple</v-icon>
        </span>
      </template>
      <span>Düzenle</span>
        </v-tooltip>
              </v-col>
              <v-col cols="12" sm="12" md="12">
                <v-text-field :error-messages="errName" :label="'Servis Yetkilisi Adı Soyadı* '" v-model="user.fullname" required></v-text-field>
              </v-col>
              <v-col cols="12" sm="12" md="12">
                <v-text-field :error-messages="errBusName" :label="'Servis Adı*'"  v-model="user.busName" hint="Servis adını giriniz."></v-text-field>
              </v-col>
              <v-col cols="12" sm="12" md="12">
                <v-text-field :error-messages="errBusLicense" :label="'Servis Plakası*'"  v-model="user.busLicense" hint="Öğrencileri getirecek servisin plakasını giriniz."></v-text-field>
              </v-col>
              <v-col cols="12" sm="12" md="12">
                <v-text-field
                @blur="phoneControl()"
                :error-messages="errPhone"
                  :label="'Telefon*'"
                  hint="Telefon numarasını doğru formatta giriniz."
                   v-model="user.phone"

                  required
                ></v-text-field>
              </v-col>
              <v-col cols="12">
              <h3>   Kullanıcı Adı : {{user.username}}  <small class="text-danger float-right">(Kullanıcı Adı değiştirilemez!)</small></h3>
              </v-col>
              <v-col cols="12" class="text-right">
                <p class="text-muted">Eğer Kullanıcı şifresini unuttuysa yeni şifre gönder.</p>
           <v-btn class="ma-2" color="primary" @click="dialog3=true" dark>{{'Şifre Değiştir'}}</v-btn>
           <p><small>* Yeni şifre telefona bildirim olarak gönderilecektir. Şifrenin telefona gönderilebilmesi için hesaba daha önce giriş yapılmış olması gerekmektedir.</small></p>
           <p v-if="newPass!=''" class="text-danger"><small>*Eğer telefona mesaj gelmediyse <span @click="confirm=true"  style="color:black;text-decoration:underline;cursor:pointer">Şifreyi Görüntüle</span></small></p>
              </v-col>
            </v-row>
          </v-container>
          <small>*Girilmesi zorunlu alanlar</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
           <v-btn  class="ma-2" @click="closeModal()" color="info" dark>{{$t('generate.close')}} </v-btn>
           <v-btn class="ma-2" :loading="loading" :disabled="loading" color="primary" @click="putUser()" dark>{{$t('generate.submit')}}</v-btn>
        </v-card-actions>
      </v-card>
     <snack-bar :text="textMsg" :snackbar="snackbar" @finished="snackbar=$event"/>
      <confirm @confirm="sendPassword()" text="Kullanıcının şifresini değiştiriyorsunuz. Onayladıktan sonra Ekranda gözükecek şifre ile giriş yapabilir.Bu işlemi yapmak istiyor musunuz?" @cancel="dialog3=false" :dialog="dialog3" />
      <confirm hide @confirm="confirm=false" :text="`Yeni Şifreniz : ${newPass} Sisteme giriş yapıldıktan sonra Kullanıcının şifresini değiştirmesini söyleyiniz!`" @cancel="confirm=false" :dialog="confirm" />
  </v-dialog>
</template>

<script>
import {phoneNumberControl} from '../../plugins/rules'
export default {
props:{
    dialog:{
        default:false,
        type : Boolean,
        required:true
    },
     user:{
        default:()=>{},
        type : Object,
        required:true
    },
      index:{
        default:-1,
        type : Number,
        required:true
    },
},
data(){
  return{
    imageData:"",
    snackbar:false,
    newImage:null,
    didntSend:"",
    successmessages:[],
    errormessages:[],
    errBusName:[],
    errBusLicense:[],
    loading:false,
    errPhone:[],
    err:"",
    errLastname:[],
    dialog3:false,
    newPass:"",
    errName:[],
    textMsg:"",
    confirm:false,
  }
},
watch:{
  'user.busLicense'(data){
    this.user.busLicense = data.toUpperCase()
  },
},
methods:{
  phoneControl(){
    let result = phoneNumberControl(this.user.phone)
    if(result){
      this.errPhone.push(result)
    }
    else{
      this.errPhone=[]
    }
  },
  putUser(){
      this.err=''
    this.errPhone=[]
    this.errBusName=[]
    this.errBusLicense=[]
    this.errName=[]
    this.loading=true
  if(this.newImage==null){
    if(this.user.fullname==''||this.user.busName==''||this.user.busLicense==''||this.user.phone==''){
      if(this.user.fullname==''){
     this.errName.push('Ad boş geçilemez.')
   }
   if(this.user.busName==''){
     this.errBusName.push('Soyad boş geçilemez.')
   }
   if(this.user.busLicense==''){
     this.errBusLicense.push('Soyad boş geçilemez.')
   }
   if(this.user.phone.length<9){
          this.errPhone.push('Eksik veya yanlış bir telefon numarası girdiniz.')
    }
     this.loading=false
    }
    else{
      this.$api.put('host',this.user.id,{
        fullname:this.user.fullname,
        busName:this.user.busName,
        busLicense:this.user.busLicense,
        phone:this.user.phone
      }).then(res=>{
     if(res.result==1){
    this.$emit('setPutUser',{index:this.index,user:this.user,image:this.imageData})
    this.loading=false
    this.dialog = false
   setTimeout(() => {
      this.closeModal()
   }, 100);
     }
     else{
        this.err = "Kullanıcı'da herhangi bir değişiklik yapmadınız."
    this.loading=false
     }

    })
    }
  }
  else{
    if(this.user.fullname==''||this.user.busName==''||this.user.busLicense==''||this.user.phone==''){
      if(this.user.fullname==''){
     this.errName.push('Ad boş geçilemez.')
   }
   if(this.user.busName==''){
     this.errBusName.push('Soyad boş geçilemez.')
   }
   if(this.user.busLicense==''){
     this.errBusLicense.push('Soyad boş geçilemez.')
   }
   if(this.user.phone.length<9){
          this.errPhone.push('Eksik veya yanlış bir telefon numarası girdiniz.')
    }
     this.loading=false
    }
    else{
      let fd = new FormData()
    fd.append('image',this.newImage)
    fd.append('fullname',this.user.fullname)
    fd.append('busName',this.user.busName)
    fd.append('busLicense',this.user.busLicense)
    fd.append('phone',this.user.phone)
    this.$api.putfd('host',this.user.id,fd).then(res=>{
      if(res.status=='success'){
      this.$emit('setPutUser',{index:this.index,user:this.user,image:this.imageData})
        this.loading=false
        this.closeModal()
      }
    })
    }

  }
  },
  controlUsername(){
    this.loading=true
      this.$api.get('user?filter=username="'+this.user.username+'"').then(res=>{
        if(res.count==0){
            this.successmessages=[]
                this.errormessages=[]
          this.successmessages.push('Bu kullanıcı adı kullanılabilir.')
           this.loading=false
        }
        else{
            this.successmessages=[]
          this.errormessages=[]
          this.errormessages.push(`${this.user.username} adlı kullanıcı ismi kullanılıyor.`)
           this.loading=false

        }
      })
    },
  closeModal(){
    this.newPass=""
    this.$emit('dialog',false);
    this.newImage=null
    setTimeout(() => {
      this.imageData=""
    this.dialog = false
    }, 250);
  },
  sendPassword(){
    let password=[]
    for (let i = 0; i < 6; i++) {
     let number = Math.ceil(Math.random()*10)
     password.push(number)
    }
    this.$api.put('host',this.user.id,{password:password.join('')}).then(res=>{
      if(res.result==1){
        this.didntSend = "Kullanıcı Daha önce telefon ile giriş yapmadı için telefonuna bildirim gönderilemedi."
        this.dialog3=false
             this.newPass = password.join('')
             this.confirm=true
         }
    })
  },
  setImage(ev){
    this.newImage = ev.target.files[0]
    let reader = new FileReader()
    var vm = this;

      reader.onload = e => {
        vm.imageData = e.target.result;
      };
      reader.readAsDataURL(ev.target.files[0]);
  }
}
}
</script>

<style>

</style>